(function() {
  'use strict';

  angular
    .module('rapCypher')
    .directive('acmeNavbar', acmeNavbar);

  /** @ngInject */
  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
          creationDate: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController(moment) {
      var vm = this;

      // "vm.creationDate" is available by directive option "bindToController: true"
      vm.relativeDate = moment(vm.creationDate).fromNow();
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('rapCypher')
    .factory('githubContributor', githubContributor);

  /** @ngInject */
  function githubContributor($log, $http) {
    var apiHost = 'https://api.github.com/repos/Swiip/generator-gulp-angular';

    var service = {
      apiHost: apiHost,
      getContributors: getContributors
    };

    return service;

    function getContributors(limit) {
      if (!limit) {
        limit = 30;
      }

      return $http.get(apiHost + '/contributors?per_page=' + limit)
        .then(getContributorsComplete)
        .catch(getContributorsFailed);

      function getContributorsComplete(response) {
        return response.data;
      }

      function getContributorsFailed(error) {
        $log.error('XHR Failed for getContributors.\n' + angular.toJson(error.data, true));
      }
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('rapCypher')
    .directive('acmeMalarkey', acmeMalarkey);

  /** @ngInject */
  function acmeMalarkey(malarkey) {
    var directive = {
      restrict: 'E',
      scope: {
        extraValues: '='
      },
      template: '&nbsp;',
      link: linkFunc,
      controller: MalarkeyController,
      controllerAs: 'vm'
    };

    return directive;

    function linkFunc(scope, el, attr, vm) {
      var watcher;
      var typist = malarkey(el[0], {
        typeSpeed: 40,
        deleteSpeed: 40,
        pauseDelay: 800,
        loop: true,
        postfix: ' '
      });

      el.addClass('acme-malarkey');

      angular.forEach(scope.extraValues, function(value) {
        typist.type(value).pause().delete();
      });

      watcher = scope.$watch('vm.contributors', function() {
        angular.forEach(vm.contributors, function(contributor) {
          typist.type(contributor.login).pause().delete();
        });
      });

      scope.$on('$destroy', function () {
        watcher();
      });
    }

    /** @ngInject */
    function MalarkeyController($log, githubContributor) {
      var vm = this;

      vm.contributors = [];

      activate();

      function activate() {
        return getContributors().then(function() {
          $log.info('Activated Contributors View');
        });
      }

      function getContributors() {
        return githubContributor.getContributors(10).then(function(data) {
          vm.contributors = data;

          return vm.contributors;
        });
      }
    }

  }

})();

(function () {
  'use strict';

  angular
    .module('rapCypher')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($log, $scope, DataService) {
    var vm = this;

    vm.videos = '';
    vm.commentText = '';

    vm.uploadedVideos = DataService.uploadedVideos;
    vm.comments = DataService.comments;

    vm.mess = 'Rap Cyper';
    $log.log("ok");


    vm.iconClicked = function (index) {
      $log.log("clicked movie icon: " + index);
      $('#data-vid').html("<video width='400' controls autoplay><source id='vid-source' src='" + DataService.uploadedVideos[index] + "' type='video/mp4'></video>");

    };

    $scope.filesChanged = function (elm) {
      vm.videos = elm.files;
      $log.log("changing the files! " + elm.files);
      $scope.$apply();

    };

    vm.videoUploaded = function () {
      $log.log("uploaded!");
    };

    vm.commentSubmit = function () {
      $log.log("submitted: " + vm.commentText);

      var comment = {
        text: vm.commentText,
        user: 'annonymous'

      };

      vm.comments.unshift(comment);
      vm.commentText = '';
    };

    vm.videoEnd = function () {
      $log.log("video ended!");
    };

    vm.previousBtnClicked = function () {
      $log.log("previous button clicked!");

      if (DataService.video.currentIndex > 0) {
        DataService.video.currentIndex--;
      }

      if (DataService.uploadedVideos[DataService.video.currentIndex] != null) {

        $log.log("changing src ");
        // $('#vid-source').src = DataService.uploadedVideos[0];
        $('#data-vid').html("<video width='400' controls autoplay><source id='vid-source' src='" + DataService.uploadedVideos[DataService.video.currentIndex] + "' type='video/mp4'></video>");


      }
    };

    var takePicture = $("#take-picture");
    takePicture.onchange = function (event) {

      $log.log("changing!");
      // Get a reference to the taken picture or chosen file
      var files = event.target.files,
        file;
      if (files && files.length > 0) {
        file = files[0];
        vm.video = file;
      }
    };


  }
})();

(function () {
  'use strict';

  angular
    .module('rapCypher')
    .directive('fileInput', function ($parse, $log, DataService) {
      return {
        restrict: 'A',
        link: function (scope, elm, attrs) {
          elm.bind('change', function () {
            $parse(attrs.fileInput)
              .assign(scope, elm[0].files);

            $log.log(elm[0]);
            $log.log(elm[0].files);
            $log.log(elm[0].files[0]);

            renderVideo(elm[0].files[0]);
            scope.$apply()

            //this function is called when the input loads a video
            function renderVideo(file) {
              var reader = new FileReader();
              reader.onload = function (event) {
                var the_url = event.target.result;
                DataService.uploadedVideos.push(the_url);
                scope.$apply();
                DataService.video.currentIndex = DataService.uploadedVideos.length - 1;

                $log.log("There have been " + DataService.uploadedVideos.length + " videos uploaded.");

                $('#data-vid').html("<video  width='400' id='actual-video' controls autoplay><source id='vid-source' src='" + the_url + "' type='video/mp4'></video>");
                $('#name-vid').html(file.name);
                $('#size-vid').html(humanFileSize(file.size, "MB"));
                $('#type-vid').html(file.type);


                $log.log('video html: ' + $('#actual-video'));


                document.getElementById('actual-video').addEventListener('ended', myHandler, false);
                function myHandler(e) {
                  $log.log("yo yo");


                  if (DataService.video.currentIndex > 0) {
                    DataService.video.currentIndex--;


                    if (DataService.uploadedVideos[DataService.video.currentIndex] != null) {

                      $log.log("changing src ");
                      // $('#vid-source').src = DataService.uploadedVideos[0];
                      $('#data-vid').html("<video width='400' controls autoplay><source id='vid-source' src='" + DataService.uploadedVideos[DataService.video.currentIndex] + "' type='video/mp4' autoplay></video>");

                      uploadtoS3(DataService.uploadedVideos[DataService.video.currentIndex]);

                    }
                  }

                  // What you want to do after the event
                }

                function uploadtoS3() {

                }

                function end() {
                  $log.log("end it.")
                }

              };

              //when the file is read it triggers the onload event above.
              reader.readAsDataURL(file);
            }

            function humanFileSize(bytes, si) {
              var thresh = si ? 1000 : 1024;
              if (bytes < thresh) return bytes + ' B';
              var units = si ? ['kB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'] : ['KiB', 'MiB', 'GiB', 'TiB', 'PiB', 'EiB', 'ZiB', 'YiB'];
              var u = -1;
              do {
                bytes /= thresh;
                ++u;
              } while (bytes >= thresh);
              return bytes.toFixed(1) + ' ' + units[u];
            }


          })
        }
      };
    });

})();

